<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>baseStyle</td>
<td>基础分页器样式(需要覆盖默认样式)</td>
<td>Object</td>
<td>—</td>
<td>{}</td>
</tr>
<tr>
<td>activeStyle</td>
<td>活动分页器样式</td>
<td>Object</td>
<td>—</td>
<td>{backgroundColor:'red'}</td>
</tr>
<tr>
<td>images</td>
<td>图片和标题列表</td>
<td>Array</td>
<td>{img:string,tit:string}</td>
<td>—</td>
</tr>
<tr>
<td>delay</td>
<td>轮播间隔</td>
<td>Number</td>
<td>—</td>
<td>3000</td>
</tr>
</tbody>
</table>
