<template><section><h1>Search</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
  <b-search @search="searchParent" @del="delParent" :history-items="historyItems"></b-search>
</div>

<demo-block>
                        <div slot="desc"><div class="demo-block">
  <b-search @search="searchParent" @del="delParent" :history-items="historyItems"></b-search>
</div>
</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">b-search</span> <span class="hljs-attr">@search</span>=<span class="hljs-string">"searchParent"</span> <span class="hljs-attr">@del</span>=<span class="hljs-string">'delParent'</span> <span class="hljs-attr">:history-items</span>=<span class="hljs-string">'historyItems'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-search</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-javascript">&lt;script&gt;
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">historyItems</span>: [
          <span class="hljs-string">'i'</span>,
          <span class="hljs-string">'love'</span>,
          <span class="hljs-string">'bilibili'</span>,
        ]
      }
