</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>属性提供可描述输入字段预期值的提示信息（hint）</td>
<td>String</td>
<td>—</td>
<td>请输入内容</td>
</tr>
<tr>
<td>history-items</td>
<td>历史搜索记录</td>
<td>Array</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>search</td>
<td>搜索事件鼠标点击触发(参数为搜索/点击字符串)</td>
<td>-</td>
</tr>
<tr>
<td>cancel</td>
<td>搜索事件鼠标点击触发(参数为搜索/点击字符串)</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        historyItems: [
          'i',
          'love',
          'bilibili',
        ]
      }
    },
    methods:{
      searchParent(val){
        alert(val)
      },
      delParent(val){
        alert(val)
      }
    }
  }
</script>


