<script>
  export default {
    data() {
      return {
        tabs: [
          {
            content: '有新动态',
            id: 0
          },
          {
            content: '最新投稿',
            id: 1
          }
        ]
      }
    },
    methods:{
      changeTab(id){
        alert(id)
      },
    }
  }
</script>


